import { createRouter, createWebHashHistory } from 'vue-router'
import DefaultLayout from '@/components/layouts/defalut.vue'

const routes = [
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '',
                redirect: '/login'
            },
            {
                path: 'art-cycle',
                name: 'ArtCycle',
                component: () => import('@/views/ArtCycle.vue')
            },
            {
                path: 'fertilization',
                name: 'Fertilization',
                component: () => import('@/views/Fertilization.vue')
            },
            {
                path: 'cycle-monitoring',
                name: 'CycleMonitoring',
                component: () => import('@/views/CyccleMonitoring.vue')
            },
            {
                path: 'opu',
                name: 'OPU',
                component: () => import('@/views/OPU.vue')
            },
            {
                path: 'semen',
                name: 'Semen',
                component: () => import('@/views/Semen.vue')
            },
            {
                path: 'semen-cryo-storage',
                name: 'SemenCryoStorage',
                component: () => import('@/views/SemenCryoStorage.vue')
            },
            {
                path: 'pregnancy',
                name: 'Pregnancy',
                component: () => import('@/views/Pregnancy.vue')
            },
            {
                path: 'embryo-transfer',
                name: 'EmbryoTransfer',
                component: () => import('@/views/EmbryoTransfer.vue')
            },
            {
                path: 'cryo-storage',
                name: 'CryoStorage',
                component: () => import('@/views/CryoStorage.vue')
            },
            {
                path: 'blood-tests',
                name: 'BloddTests',
                component: () => import('@/views/BloddTests.vue')
            },
            {
                path: 'daily-report',
                name: 'DailyReport',
                component: () => import('@/views/DailyReport.vue')
            },
            {
                path: 'cycle-report',
                name: 'CycleReport',
                component: () => import('@/views/CycleReport.vue')
            },
            {
                path: 'patient-mas-log',
                name: 'PatientMasLog',
                component: () => import('@/views/PatientMasLog.vue')
            },
            {
                path: 'message-inbox',
                name: 'MessageInbox',
                component: () => import('@/views/MessageInbox.vue')
            },
            {
                path: 'born',
                name: 'Born',
                component: () => import('@/views/Born.vue')
            },
            {
                path: 'test',
                name: 'test',
                component: () => import('@/views/Test.vue')
            }
        ]
    },
    {
        path: '/login',
        component: () => import('@/views/Login.vue'),
        name: 'Login'
    },
    {
        path: '/:catchAll(.*)',
        component: () => import('@/views/NotFound.vue')
    }
];


const router = createRouter({
    history: createWebHashHistory(process.env.BASE_URL),
    routes
})

router.beforeEach((to, from, next) => {
    const isAuthenticated = localStorage.getItem('user')

    if (to.name !== 'Login' && !isAuthenticated) next({ name: 'Login' })
    else next()
})

export default router
